html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: var(--very-light-gray);
}
@media (max-width: 87.4375em) {
  body {
    height: 150vh;
  }
}

:root {
  --bright-orange: hsl(31, 77%, 52%);
  --dark-cyan: hsl(184, 100%, 22%);
  --very-dark-cyan: hsl(179, 100%, 13%);
  --transp-white: hsla(0, 0%, 100%, 0.75);
  --very-light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-big-shoulders: "Big Shoulders Display", cursive;
  --font-lexend: "Lexend Deca", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

h1 {
  font-family: var(--font-big-shoulders);
}

p,
button {
  font-family: var(--font-lexend);
}

.hero {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero__container {
  margin: 0 10rem;
  display: flex;
  border-radius: 5px;
  overflow: hidden;
}
@media (max-width: 87.4375em) {
  .hero__container {
    min-width: 18em;
    flex-direction: column;
  }
}
.hero__container .card {
  width: 18em;
  padding: 2.5rem 4rem;
}
.hero__container .card__sedans {
  background-color: var(--bright-orange);
}
.hero__container .card__suvs {
  background-color: var(--dark-cyan);
}
.hero__container .card__luxury {
  background-color: var(--very-dark-cyan);
}
.hero__container .card img {
  padding-bottom: 1.5rem;
}
.hero__container .card h1 {
  font-size: 2rem;
  color: var(--very-light-gray);
  text-transform: uppercase;
  padding-bottom: 1.5rem;
}
.hero__container .card p {
  font-size: 0.8rem;
  color: var(--transp-white);
  padding-bottom: 4rem;
  line-height: 1.5;
}
.hero__container .card .btn {
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  border: 1px solid white;
  transition: all 0.5s ease-in-out;
}
.hero__container .card .btn__sedans {
  color: var(--bright-orange);
}
.hero__container .card .btn__suvs {
  color: var(--dark-cyan);
}
.hero__container .card .btn__luxury {
  color: var(--very-dark-cyan);
}
.hero__container .card .btn:hover {
  background-color: transparent;
  color: white;
  border: 1px solid white;
}